









export default {
	data(){
		return{
			name : ''
		}
	},
	methods : {
		inputName(e){
			this.name = e.detail.value;
		},
		saveChangeName(){
			uni.showLoading({
				title : '正在处理...'
			})
			setTimeout(()=> {
				uni.hideLoading();
				uni.showToast({
					title: '操作成功!',
					icon: 'success',
					duration: 1500,
					success : function(){
						uni.navigateBack({
							delta: 1
						})
					}
				})
			}, 1500)
		}
	},
	onLoad(options){
		if(options.name) this.name = options.name;
	}
}
